import useStores from 'hooks/useStores'
import iconClose from 'icons/icon_close.png'
import { observer } from 'mobx-react-lite'
import { Scrollbars } from 'react-custom-scrollbars-2'
import { legendIconsDark, legendIconsLight } from 'utils/icons/legendIcons'
import './Legend.css'

export default observer(function Legend() {
	const { appStore, legendStore } = useStores()

	const open: string = legendStore.open ? 'open' : ''

	return (
		<Scrollbars autoHide style={{ height: '100%' }}>
			<div
				onMouseLeave={legendStore.setClose}
				className={`legend ${appStore.theme} ${open}`}
			>
				<div className="legend__close" onClick={legendStore.setClose}>
					<img src={iconClose} alt="" />
				</div>
				<div className="legend__col">
					<div className="legend__section">
						<div className="legend__title">
							Операции выбытия из прослеживаемости
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon12} alt="" />
							</div>
							<div className="legend__text">
								Захоронение, обезвреживание, утилизация или уничтожение
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon3} alt="" />
							</div>
							<div className="legend__text">
								Использование в производстве, в т.ч. переработке
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon4} alt="" />
							</div>
							<div className="legend__text">
								Уничтожение или безвозвратная утрата товаров вследствие действия
								непреодолимой силы
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon5} alt="" />
							</div>
							<div className="legend__text">
								Передача или реализация в розничной торговле
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon8} alt="" />
							</div>
							<div className="legend__text">
								Фактический вывоз с таможенной территории РФ в соответствии с
								таможенной процедурой экспорта или реэкспорта
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon18} alt="" />
							</div>
							<div className="legend__text">
								Реализация дипломатическим представительствам
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon7} alt="" />
							</div>
							<div className="legend__text">Конфискация государством</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon1} alt="" />
							</div>
							<div className="legend__text">
								Безвозмездная передача физическому лицу
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon11} alt="" />
							</div>
							<div className="legend__text">
								Недостача товара при инвентаризации
							</div>
						</div>
					</div>

					<div className="legend__section">
						<div className="legend__title">
							Операции возобновления прослеживаемости
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon6} alt="" />
							</div>
							<div className="legend__text">Возврат товара из розницы</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon13} alt="" />
							</div>
							<div className="legend__text">
								Выявление товара при инвентаризации
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon38} alt="" />
							</div>
							<div className="legend__text">
								Возврат неиспользованного товара из производства
							</div>
						</div>
					</div>

					<div className="legend__section">
						<div className="legend__title">Другие индикаторы</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon2} alt="" />
							</div>
							<div className="legend__text">Зарегистрированные остатки</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon1} alt="" />
							</div>
							<div className="legend__text">
								Физ.лицо, не связанное с осуществлением предпринимательской
								деятельности
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon26} alt="" />
							</div>
							<div className="legend__text">Пиктограма остатка</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon15} alt="" />
							</div>
							<div className="legend__text">
								Ввод в оборот конфискованного товара
							</div>
						</div>
					</div>
				</div>

				<div className="legend__col legend__right">
					<div className="legend__section">
						<div className="legend__title">Риски</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon9} alt="" />
							</div>
							<div className="legend__text">Пересортица</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon10} alt="" />
							</div>
							<div className="legend__text">
								Завышенная стоимость реализации
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon23} alt="" />
							</div>
							<div className="legend__text">
								Заниженная стоимость реализации
							</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon24} alt="" />
							</div>
							<div className="legend__text">Отрицательный остаток</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon25} alt="" />
							</div>
							<div className="legend__text">Остаток по посреднику</div>
						</div>

						<div className="legend__item">
							<div className="legend__image">
								<div className="color-icon hight"></div>
							</div>
							<div className="legend__text">НП с высоким уровнем риска</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<div className="color-icon medium"></div>
							</div>
							<div className="legend__text">НП со средним уровнем риска</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<div className="color-icon low"></div>
							</div>
							<div className="legend__text">НП с низким уровнем риска</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<div className="color-icon"></div>
							</div>
							<div className="legend__text">Уровень риска не определен</div>
						</div>
					</div>

					<div className="legend__section">
						<div className="legend__title">Индикаторы связи</div>
						<div className="legend__item">
							<div className="legend__image arrow">
								<img src={legendIconsLight.icon27} alt="" />
							</div>
							<div className="legend__text">Переход права собственности</div>
						</div>
						<div className="legend__item">
							<div className="legend__image arrow">
								<img src={legendIconsLight.icon28} alt="" />
							</div>
							<div className="legend__text">Посредническая сделка</div>
						</div>
						<div className="legend__item">
							<div className="legend__image arrow">
								<img src={legendIconsLight.icon33} alt="" />
							</div>
							<div className="legend__text">Потенциальные контрагенты</div>
						</div>
						<div className="legend__item">
							<div className="legend__image arrow">
								<img src={legendIconsLight.icon37} alt="" />
							</div>
							<div className="legend__text">
								Операции с товаром, входящим в партию, выпущенные по заявлениям
								до декларирования товара
							</div>
						</div>
					</div>

					<div className="legend__section">
						<div className="legend__item">
							<div className="legend__image">
								<img
									src={
										appStore.theme === 'dark'
											? legendIconsLight.icon34
											: legendIconsDark.icon34
									}
									alt=""
								/>
							</div>
							<div className="legend__text">Скрыть/показать дубликаты</div>
						</div>
						<div className="legend__item">
							<div className="legend__image">
								<img src={legendIconsLight.icon35} alt="" />
							</div>
							<div className="legend__text">Направление сделок</div>
						</div>
					</div>

					<div className="legend__section">
						<div className="legend__item">
							<div className="legend__image last">
								<img
									src={
										appStore.theme === 'dark'
											? legendIconsLight.icon36
											: legendIconsDark.icon36
									}
									alt=""
								/>
							</div>
							<div className="legend__text">
								Группировать/разгруппировать узлы
							</div>
						</div>
					</div>
				</div>
			</div>
		</Scrollbars>
	)
})
