import useStores from 'hooks/useStores'
import { observer } from 'mobx-react-lite'
import './Loader.css'

export default observer(function Loader() {
	const { appStore } = useStores()
	return (
		<div className={`loader ${appStore.isLoading ? 'open' : 'close'}`}>
			Выгрузка..
		</div>
	)
})
