import Card from 'components/shared/Card'
import TableHeader from 'components/shared/TableHeader'
import { Loader, Pagination } from 'components/vsa'
import useStores from 'hooks/useStores'
import { observer } from 'mobx-react-lite'
import Item from './Item'
import styles from './List.module.scss'

export default observer(function List() {
	const { infoCardStore } = useStores()
	const {
		listData,
		pagination,
		meta,
		isListLoading,
		changePage,
		activeSort,
		sortDesc,
		setSort,
	} = infoCardStore

	const sortItems = [
		{
			text: 'Номер товара',
			value: 'productNumber',
			className: `${styles.productNumber} ${styles.startSection}`,
		},
		{
			text: 'Наименование',
			value: 'productName',
			className: `${styles.productName}`,
		},
		{
			text: 'Код',
			value: 'tnved',
			className: `${styles.tnved}`,
		},
		{
			text: 'Количество',
			value: 'quantity',
			className: `${styles.quantity}`,
		},
		{
			text: 'Цена',
			value: 'costRub',
			className: `${styles.costRub} ${styles.endSection}`,
		},
		{
			text: 'Идент. номер',
			value: 'sellerId',
			className: `${styles.sellerId} ${styles.startSection}`,
		},
		{
			text: 'Наименование',
			value: 'sellerName',
			className: `${styles.sellerName}`,
		},
		{
			text: 'Место регистрации',
			value: 'sellerAddress',
			className: `${styles.sellerAddress} ${styles.endSection}`,
		},
		{
			text: 'Идент. номер',
			value: 'buyerId',
			className: `${styles.buyerId} ${styles.startSection}`,
		},
		{
			text: 'Наименование',
			value: 'buyerName',
			className: `${styles.buyerName}`,
		},
		{
			text: 'Место регистрации',
			value: 'buyerAddress',
			className: `${styles.buyerAddress} ${styles.endSection}`,
		},
		{
			text: 'Вид',
			value: 'docTypeName',
			className: `${styles.docTypeName} ${styles.startSection}`,
		},
		{
			text: 'Рег. номер',
			value: 'docId',
			className: `${styles.docId}`,
		},
		{
			text: 'Дата',
			value: 'docDate',
			className: `${styles.docDate}`,
		},
		{
			text: 'Серия и номер накладной',
			value: 'invoiceSer',
			className: `${styles.invoiceSer} ${styles.endSection}`,
		},
	]

	return (
		<div className={styles.list}>
			{isListLoading && <Loader className="loader" />}
			{listData?.length ? (
				<div className={styles.list__table}>
					<table>
						<thead className={styles.list__header}>
							<tr>
								<th colSpan={5}>
									<span>Товар</span>
								</th>
								<th colSpan={3}>
									<span>Продавец</span>
								</th>
								<th colSpan={3}>
									<span>Покупатель</span>
								</th>
								<th colSpan={4}>
									<span>Сопроводительный документ</span>
								</th>
							</tr>
						</thead>
						<TableHeader
							className={styles.list__subheader}
							activeValue={activeSort}
							sortDesc={sortDesc}
							items={sortItems}
							onSort={setSort}
							type="sort"
						/>
						<tbody>
							{listData.map((item, i) => (
								<Item key={i} item={item} />
							))}
						</tbody>
					</table>
				</div>
			) : (
				<Card>
					<div className="registry__nodata">Данные не найдены</div>
				</Card>
			)}
			{meta.pagesCount && listData?.length ? (
				<div className="registry__pagination">
					<div className="registry__total">
						Всего записей: {meta.totalRowsCount}
					</div>
					<Pagination
						onChange={changePage}
						pagination={{
							page: pagination.page,
							count: pagination.count,
							total: pagination.total,
						}}
						showJumpTo={true}
					/>
				</div>
			) : null}
		</div>
	)
})
